$bottomgutter: 0.5em;
$gutter: 0.75em;
$flex-basis: calc(50% - #{$gutter*2});
$mobile-flex-basis: calc(100% - #{$gutter*2});

$major-desc-gutter: 2.6em;
$marjor-desc-padding: 45px;

.section-project-desc {
  display: flex;
  flex: 0 1 $flex-basis;  
  padding: 0 $gutter $bottomgutter;
  position: relative;
  overflow: hidden;
  max-height: 50vh;
  flex-direction: column;
  @media (max-width: 768px) {
    flex: 1 0 $mobile-flex-basis;
  }

  &.small {
    flex: 1 0 calc(40% - #{$gutter*2});
  }
  &.large {
    flex: 1 0 calc(60% - #{$gutter*2});
  }

  &.full {
    flex: 1 0 $mobile-flex-basis;
  }

  p {
    margin: $gutter*2 $gutter*3;
    &.project-major-desc {
      font-size: 1.3em;
      line-height: 1.8em;
      padding: 0 $marjor-desc-padding;
      @media (max-width: 768px) {
        padding: 0;
        margin-left: 0;
        margin-right: 0;
      }  
    }
  }

  .desc-wrapper {
    margin: $gutter*2 $marjor-desc-padding;
    @media (max-width: 768px) {
      margin-left: 0;
      margin-right: 0;
    }  
    .desc-title {
      font-weight: 700;
      font-size: 0.9em;
      line-height: 1.3em;
      text-transform: uppercase;
      color: #CCC;
    }
    .desc-content {
      line-height: 1.3em;
      font-size: 0.8em;
    }
  }

  .section-project-desc-button-wrapper {
    margin: 0 $major-desc-gutter;
    @media (max-width: 768px) {
      margin-left: 0;
      margin-right: 0;
    }  
    a {
      margin: 0 $marjor-desc-padding;
      @media (max-width: 768px) {
        margin: 0 auto;
      }
      &:focus {
        outline: none;
      }
    }
  }
  .section-project-desc-button {
    width: 300px;
    height: 60px;
    background-color: white;
    color: #777;
    border: 1px solid #777;
    &:hover {
      background-color: #f7f7f7;
    }
    &:focus {
      color: #555;
      outline: 1px solid #555;
    }

    // margin-left: $major-desc-gutter;
    
    @media (max-width: 768px) {
      width: 100%;
    }  
  }
}

